.Button {
  color: white;
  transition: background-color 0.3s;
  z-index: 10;
}
.Button:hover {
  cursor: pointer;
}

.LinkButton {
  width: 100px;
  margin-top: 20px;
  padding: 10px;
  border: none;
  background-color: #007bff;
  color: white;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  text-decoration: none;
  text-align: center;
}
.LinkButton:hover {
  background-color: #0056b3;
}

.coinCardContainer {
  border: 2px solid #1E1F25;
  border-radius: 12px;
  padding: 30px;
  width: 150px;
  background-color: #131517;
  box-shadow: 7px 5px 5px #101013;
}
.coinCardContainer:hover {
  transition: 0.3s;
  cursor: pointer;
  background-color: #1E1F25;
}
.coinCardContainer .coinCardIcon {
  height: 48px;
  width: 48px;
}
.coinCardContainer .coinCardNameContainer {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.coinCardContainer .coinCardNameContainer .coinCardSymbol, .coinCardContainer .coinCardNameContainer .coinCardName {
  color: white;
  line-height: 1.2rem;
}
.coinCardContainer .coinCardNameContainer .coinCardSymbol {
  font-size: 1.2rem;
  margin: auto 0;
}
.coinCardContainer .coinCardNameContainer .coinCardName {
  margin-left: 10px;
  font-size: 1rem;
  border-left: 2px solid #fff;
  padding-left: 10px;
}
.coinCardContainer .coinCardPercentContainer {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
.coinCardContainer .coinCardPercentContainer .coinCardPercent {
  margin-left: 5px;
}
.coinCardContainer .coinCardPrice {
  margin-top: 10px;
  color: #fff;
  font-weight: bold;
  font-size: 1.5rem;
}

.tableHeader {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
}
.tableHeader .tableParameters, .tableHeader .tableParametersSort {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tableHeader .tableParameters .tableParametersText, .tableHeader .tableParametersSort .tableParametersText {
  color: #808A9A;
}
.tableHeader .tableParameters .tableParametersImg, .tableHeader .tableParametersSort .tableParametersImg {
  height: 10px;
  width: 10px;
  margin-left: 5px;
}
.tableHeader .tableParametersSort:hover {
  cursor: pointer;
}

@media (min-width: 1441px) and (max-width: 1550px) {
  .coinCardContainer {
    padding: 30px;
  }
}
@media (min-width: 1101px) and (max-width: 1440px) {
  .coinCardContainer {
    padding: 30px;
  }
  .coinCardContainer .coinCardPrice {
    font-size: 1.3rem;
  }
}
@media (min-width: 691px) and (max-width: 1101px) {
  .coinCardContainer {
    padding: 30px;
  }
  .coinCardContainer .coinCardPrice {
    font-size: 1.3rem;
  }
}/*# sourceMappingURL=reusableComponents.module.css.map */