.coinContainer {
  display: grid;
  align-items: center;
  margin: 15px 0px;
}
.coinContainer .coinIcon {
  width: 32px;
  height: 32px;
  margin: 0 auto;
}
.coinContainer .coinNameContainer {
  display: flex;
  justify-content: center;
}
.coinContainer .coinNameContainer .coinSymbol {
  color: #fff;
  font-size: 1.2rem;
  line-height: 10px;
  border-right: solid 1px #fff;
  padding: 5px;
}
.coinContainer .coinPercentContainer {
  display: flex;
  justify-content: center;
}
.coinContainer .coinPercentContainer .coinPercentImg {
  margin-right: 5px;
}

.coinTableText {
  margin: auto 0;
  color: #fff;
  margin-left: 5px;
}

.coinTableBtn {
  background-color: #131517;
  padding: 0px;
  border: 1px solid #1E1F25;
  color: white;
  font-size: 0.9rem;
  line-height: 2rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  z-index: 10;
  margin: auto 0;
  white-space: nowrap;
}
.coinTableBtn span {
  margin-right: 5px;
  color: #1B70F1;
  font-size: 1.1rem;
}/*# sourceMappingURL=Coin.module.css.map */